
import { Table, Container, Button, Form } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  updateCartList,
  updateCartCount,
  deleteCart,
  orderAdd,
} from "../redux";
import { useState } from "react";
import { useNavigate } from "react-router-dom";


let orderArr = [];

const CartComponent = () => {

  let navigate = useNavigate();
  let dispatch = useDispatch();
  let productCartList = useSelector((state) => state.productCartList);

  let list = productCartList;

  // 
  const setArrOrder = () => {
    if (orderArr.length === 0) {
      alert("    .");
    } else {
      let ordrProductList = list.filter((tmp) => orderArr.includes(tmp.id.toString()));
      dispatch(orderAdd(ordrProductList));
      navigate('../order');
    }
  }

  // 
  const checkedFnc = (e) => {
    if (e.target.checked){
      orderArr.push(e.target.value);
    }else{
      let filtered = orderArr.filter((val) => val !== e.target.value);
      orderArr = filtered;
    }
  }

  return (
    <>
      <Container className="mt-3">
        <Table striped bordered hover size="sm">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {typeof list === "undefined" || list.length === 0 ? (
              <tr>
                <td colSpan={4}>
                  <p style={{ textAlign: "center" }}>
                       .
                  </p>
                </td>
              </tr>
            ) : (
              list.map((it, index) => (
                <tr key={index}>
                  <td>
                    <Form.Group className="mb-3">
                      <Form.Check
                        name="id"
                        onChange={checkedFnc}
                        value={it.id}
                        count={it.count}
                      />
                    </Form.Group>
                  </td>
                  <td>
                    {it.title}
                    <br />
                     : {it.size}
                  </td>
                  <td>
                    {it.count}&nbsp;
                    <Button
                      variant="outline-info"
                      size="sm"
                      onClick={() => {
                        dispatch(
                          updateCartCount({
                            cartSeq: it.cartSeq,
                            mode: "plus",
                          })
                        );
                      }}
                    >
                      +
                    </Button>
                    <Button
                      variant="outline-danger"
                      size="sm"
                      onClick={() => {
                        dispatch(
                          updateCartCount({
                            cartSeq: it.cartSeq,
                            mode: "minus",
                          })
                        );
                      }}
                    >
                      -
                    </Button>
                  </td>
                  <td>
                    <Button
                      variant="outline-danger"
                      size="sm"
                      onClick={() => {
                        dispatch(deleteCart(it.cartSeq));
                      }}
                    >
                      
                    </Button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </Table>
        <div>
          <Button variant="success" size="sm" onClick={setArrOrder}>
             
          </Button>
        </div>
      </Container>
    </>
  );
}

export default CartComponent;