import './App.css';
import {
  Navbar,
  Container,
  Nav,
  Button,
  Spinner,
  Offcanvas,
  Card,
  Carousel,
  Image,
} from "react-bootstrap";
import MainComponent from "./_component/MainComponent";
import DetailComponent from "./_component/DetailComponent";
import CartComponent from "./_component/CartComponent";
import OrderComponent from "./_component/OrderComponent";
import {Routes, Route, Link, useNavigate, Outlet} from 'react-router-dom';
import { useState } from "react";


function App() {

  let navigate = useNavigate();
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  let getSessionData = sessionStorage.getItem("viewProduct");
  const sessionData = JSON.parse(getSessionData);

  return (
    <>
      <Container fluid="md" className="mt-3">
        <Navbar bg="light" data-bs-theme="light">
          <Container>
            <Navbar.Brand href="/">Shoe Store(feat.React)</Navbar.Brand>
          </Container>
        </Navbar>
        <Navbar bg="light" data-bs-theme="light">
          <Container>
            <Nav className="me-auto">
              <Nav.Link onClick={() => navigate("/")}>Home</Nav.Link>
              <Nav.Link onClick={() => navigate("/cart")}>Cart</Nav.Link>
              <Nav.Link onClick={handleShow}>
                <Button variant="outline-warning" size="sm">
                  Recent Products
                </Button>
              </Nav.Link>
            </Nav>
          </Container>
        </Navbar>
        <Routes>
          <Route
            path="*"
            element={
              <div>
                <h4>404 Not Page</h4>
              </div>
            }
          />
          <Route
            path="/"
            element={
              <>
                <Carousel>
                  <Carousel.Item>
                    <div className="main-bg" />
                    <Carousel.Caption>
                      <h3>First slide label</h3>
                      <p>
                        Nulla vitae elit libero, a pharetra augue mollis
                        interdum.
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <div className="main-bg2" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <div className="main-bg3" />
                  </Carousel.Item>
                </Carousel>
                <MainComponent />
              </>
            }
          />
          <Route
            path="/detail"
            element={
              <>
                <DetailComponent />
              </>
            }
          />
          <Route
            path="/cart"
            element={
              <>
                <CartComponent />
              </>
            }
          />
          <Route
            path="/order"
            element={
              <>
                <OrderComponent />
              </>
            }
          />
        </Routes>
        <Offcanvas show={show} onHide={handleClose}>
          <Offcanvas.Header closeButton>
            <Offcanvas.Title>  </Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body>
            {sessionData && sessionData.length > 0
              ? sessionData.map((it, index) => (
                  <>
                    <Card
                      border="success"
                      style={{ width: "20rem" }}
                      key={index}
                    >
                      <Card.Header>{it.title}</Card.Header>
                      <Card.Body>
                        <Card.Text>
                          <a
                            href="javascript:"
                            onClick={() => {
                              handleClose(false);
                              navigate(`/detail?seq=${it.seq}`);
                            }}
                          >
                            <Image
                              src={`/image/${it.fileName}`}
                              width="60%"
                            />
                          </a>
                        </Card.Text>
                      </Card.Body>
                    </Card>
                    <br />
                  </>
                ))
              : null}
          </Offcanvas.Body>
        </Offcanvas>
      </Container>
    </>
  );
}

export default App;
