

import { useState } from "react";
import {
  Container,
  Form,
  Col,
  Row,
  Card,
  ListGroup,
  Table,
  Image,
  Badge,
  Ratio,
  Accordion,
  InputGroup,
  Navbar,
  Button,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import * as formik from "formik";
import * as yup from "yup";

let totalOrderPrice = 0;

const OrderComponent = () => {

  let [searchParams, setSearchParams] = useSearchParams();
  let id = searchParams.get("id");

  //
  let dtlData = useSelector((state) => {
    if (id === null) {
      return state.productOrder;
    } else {
      let list = state.productDtl;
      let arr = [];
      if (list.length > 0) {
        list.forEach((e) => {
          let strId = e.id;
          if (strId.toString() === id) {
            arr.push(e);
          }
        });
      }
      return arr;
    }
  });

  //  
  dtlData.map((val)=>{
    totalOrderPrice += Number(val.price)*Number(val.count);
  });


  //
  const [ordrInfo, setOrderInfo] = useState({
    name: "",
    hp: "",
    addr: "",
    ordrPrice: totalOrderPrice,
    usePoint: "",
    totalPoint: 300000,
    size: "",
  });

  const { name, hp, addr, ordrPrice, usePoint, totalPoint } = ordrInfo;
  const writeFormChangeFnc = (e) => {

    setOrderInfo({
      ...ordrInfo,
      [e.target.name]: e.target.value,
    });
  }
  // 
  const usePointFnc = () => {

    let uPoint = usePoint;
    let oPrice = totalOrderPrice;
    let tPoint = 300000;  //   

    if (uPoint > 0 && uPoint !== "") {
      if (uPoint > tPoint) {
        alert("    .");
        uPoint = '';
      } else {
        uPoint = uPoint > oPrice ? oPrice : uPoint;
      }
    } else {
      uPoint = usePoint;
    }
    setOrderInfo({
      ...ordrInfo,
      ordrPrice: oPrice - uPoint,
      usePoint: uPoint,
      totalPoint: tPoint - uPoint,
    });
  }


  //validation
  const { Formik } = formik;
  const [isSubmitting, setSubmitting] = useState(false);
  const schema = yup.object().shape({
    name: yup.string().required(),
    hp: yup.string().required(),
    addr: yup.string().required(),
    size: yup.string().required(),
  });

  return (
    <>
      <Formik
        validationSchema={schema}
        onSubmit={(data, { setSubmitting }) => {
          setSubmitting(true);
          setOrderInfo({
            ...ordrInfo,
            name: data.name,
            hp: data.hp,
            size: data.size,
          });
          if (
            window.confirm(
              dtlData.title + "( : " + data.size + ")  ?"
            )
          ) {
          }
          setSubmitting(false);
        }}
        initialValues={ordrInfo}
      >
        {({ handleSubmit, handleChange, values, touched, errors }) => (
          <Form noValidate onSubmit={handleSubmit}>
            <Container>
              <Card>
                <Card.Header as="h5"></Card.Header>
                <Card.Body>
                  {dtlData &&
                    dtlData.map((it, index) => (
                      <div className="mt-3">
                        <Card.Title>
                          <Image
                            src={`/image/shoes${Number(it.id) + 1}.jpg`}
                            thumbnail
                            width="20%"
                          />
                          &nbsp;{it.title}
                        </Card.Title>
                        <Card.Text>
                           :{" "}
                          <span style={{ color: "red" }}>
                            {it.price * it.count}
                          </span>
                           /  : {it.count}
                        </Card.Text>

                        <Form.Group
                          as={Col}
                          md="3"
                          controlId="validationFormik01"
                          className="mt-3"
                        >
                          <Form.Select
                            size="sm"
                            name={'size'+index}
                            value={values.size}
                            onChange={handleChange}
                            isInvalid={!!errors.size}
                          >
                            <option value="">Size</option>
                            <option value="230">230</option>
                            <option value="240">240</option>
                            <option value="250">250</option>
                            <option value="260">260</option>
                            <option value="270">270</option>
                            <option value="280">280</option>
                            <option value="290">290</option>
                          </Form.Select>
                          <Form.Control.Feedback type="invalid">
                               .
                          </Form.Control.Feedback>
                        </Form.Group>
                      </div>
                    ))}
                </Card.Body>
              </Card>

              <Card className="mt-3">
                <Card.Header as="h5"></Card.Header>
                <Card.Body>
                  <Card.Text>
                    <Row>
                      <Form.Group
                        as={Col}
                        md="3"
                        controlId="validationFormik01"
                        className="mt-3"
                      >
                        <Form.Label></Form.Label>
                        <Form.Control
                          type="text"
                          placeholder="   ."
                          name="name"
                          value={values.name}
                          onChange={handleChange}
                          isInvalid={!!errors.name}
                          maxLength={30}
                        />
                        <Form.Control.Feedback type="invalid">
                             .
                        </Form.Control.Feedback>
                      </Form.Group>

                      <Form.Group
                        as={Col}
                        md="3"
                        controlId="validationFormik02"
                        className="mt-3"
                      >
                        <Form.Label></Form.Label>
                        <Form.Control
                          type="number"
                          placeholder="   ."
                          name="hp"
                          value={values.hp}
                          onChange={handleChange}
                          isInvalid={!!errors.hp}
                          maxLength={15}
                        />
                        <Form.Control.Feedback type="invalid">
                             .
                        </Form.Control.Feedback>
                      </Form.Group>

                      <Form.Group
                        as={Col}
                        md="3"
                        controlId="validationFormik03"
                        className="mt-3"
                      >
                        <Form.Label></Form.Label>
                        <Form.Control
                          type="text"
                          placeholder="   ."
                          name="addr"
                          value={values.addr}
                          onChange={handleChange}
                          isInvalid={!!errors.addr}
                          maxLength={50}
                        />
                        <Form.Control.Feedback type="invalid">
                             .
                        </Form.Control.Feedback>
                      </Form.Group>
                    </Row>
                  </Card.Text>
                </Card.Body>
              </Card>

              <Card className="mt-3">
                <Card.Header as="h5"></Card.Header>
                <Card.Body>
                  <Card.Text>
                    <Row>
                      <Navbar className="bg-body-tertiary">
                        <Container>
                          <Navbar.Brand style={{ fontSize: "15px" }}>
                             
                          </Navbar.Brand>
                          <Navbar.Toggle />
                          <Navbar.Collapse className="justify-content-end">
                            <Navbar.Text>
                              <span style={{ fontWeight: "700" }}>
                                {totalPoint} P
                              </span>
                            </Navbar.Text>
                          </Navbar.Collapse>
                        </Container>
                      </Navbar>
                      <InputGroup className="mb-3">
                        <Form.Control
                          type="number"
                          placeholder=""
                          name="usePoint"
                          value={usePoint}
                          onChange={writeFormChangeFnc}
                          onBlur={usePointFnc}
                        />
                        <InputGroup.Text id="basic-addon2">
                          <Button
                            type="button"
                            variant="warning"
                            size="sm"
                            onClick={() => {
                              let uPoint = usePoint;
                              let oPrice = totalOrderPrice;
                              let tPoint = 300000; //   

                              if (tPoint > oPrice) {
                                uPoint = oPrice;
                              } else {
                                uPoint = tPoint;
                              }
                              setOrderInfo({
                                ...ordrInfo,
                                ordrPrice: oPrice - uPoint,
                                usePoint: uPoint,
                                totalPoint: tPoint - uPoint,
                              });
                            }}
                          >
                            
                          </Button>
                        </InputGroup.Text>
                      </InputGroup>
                    </Row>
                  </Card.Text>
                </Card.Body>
              </Card>

              <Row>
                <Navbar className="bg-body-tertiary">
                  <Container>
                    <Navbar.Brand href="#home"> </Navbar.Brand>
                    <Navbar.Toggle />
                    <Navbar.Collapse className="justify-content-end">
                      <Navbar.Text>
                        <span className="order_txt">{ordrPrice}</span>
                      </Navbar.Text>
                    </Navbar.Collapse>
                  </Container>
                </Navbar>
              </Row>

              <div className="d-grid gap-2 mt-3">
                <Button
                  type="submit"
                  variant="primary"
                  size="lg"
                  disabled={isSubmitting}
                >
                  
                </Button>
              </div>
            </Container>
            <div className="mt-3"></div>
          </Form>
        )}
      </Formik>
    </>
  );
}

export default OrderComponent;